import ScrollableAnchor from 'react-scrollable-anchor';
import React, { useState, useEffect } from 'react';
import { Carousel } from 'react-responsive-carousel';

import api from '../../services/api';

import 'react-responsive-carousel/lib/styles/carousel.min.css'; // requires a loader

import {
  PrimeiraSecao,
  SegundaSecao,
  TerceiraSecao,
  QuartaSecao,
  QuintaSecao,
  SextaSecao,
  SetimaSecao,
} from './styles';

import Stars from '../../assets/stars.svg';

interface Conteudos {
  titulo: string;
  paragrafo: string;
}

interface Botao {
  title: string;
  url: string;
  target: string;
}

interface Downloads {
  icone: {
    title: string;
    sizes: {
      thumbnail: string;
    };
  };
  botao: Botao;
}

interface Itens {
  icone: {
    title: string;
    sizes: {
      thumbnail: string;
    };
  };
  titulo: string;
  paragrafo: string;
  imagem: {
    title: string;
    sizes: {
      '1000x700': string;
    };
  };
}

interface Depoimentos {
  avatar: {
    title: string;
    sizes: {
      thumbnail: string;
    };
  };
  paragrafo: string;
  nome: string;
  aprovacao_ou_ano_atual: string;
}

interface Dados {
  primeira_secao: {
    ativar: boolean;
    background: {
      title: string;
      sizes: {
        '1920x1080': string;
      };
    };
    logo: {
      title: string;
      sizes: {
        '500x100': string;
      };
    };
    imagem_computador: {
      title: string;
      sizes: {
        '1000x700': string;
      };
    };
    titulo: string;
    paragrafo: string;
  };
  segunda_secao: {
    ativar: boolean;
    background: {
      title: string;
      sizes: {
        '1920x1080': string;
      };
    };
    titulo: string;
    paragrafo: string;
    botoes: [Downloads];
  };
  terceira_secao: {
    background: {
      title: string;
      sizes: {
        '1920x1080': string;
      };
    };
    ativar: boolean;
    conteudos: [Conteudos];
    video: string;
  };
  quarta_secao: {
    ativar: boolean;
    itens: [Itens];
  };
  quinta_secao: {
    ativar: boolean;
    botoes: [
      {
        botao: Botao;
      }
    ];
  };
  sexta_secao: {
    ativar: boolean;
    nota_do_app: string;
    paragrafo: string;
    depoimentos: [Depoimentos];
  };
  setima_secao: {
    ativar: boolean;
    imagem: {
      title: string;
      sizes: {
        '1000x700': string;
      };
    };
    paragrafo: string;
  };
  oitava_secao: {
    ativar: boolean;
    botao: {
      title: string;
      url: string;
      target: string;
    };
  };
}

const getConfigurableProps = () => ({
  showArrows: false,
  showStatus: false,
  showIndicators: true,
  infiniteLoop: true,
  showThumbs: false,
  useKeyboardArrows: true,
  autoPlay: true,
  stopOnHover: true,
  swipeable: true,
  dynamicHeight: false,
  emulateTouch: true,
  thumbWidth: 100,
  selectedItem: 0,
  interval: 7000,
  transitionTime: 150,
  swipeScrollTolerance: 5,
});

const Aplicativo: React.FC = () => {
  const [dados, setDados] = useState<Dados | null>(null);
  const [search, setSearch] = useState('');

  useEffect(() => {
    const queryString = window.location.search;
    setSearch(queryString);

    api.get(`pages/5370`).then(response => {
      setDados(response.data.acf);
    });
  }, []);

  return (
    <>
      {dados && (
        <>
          {dados.primeira_secao.ativar && (
            <PrimeiraSecao
              bg={dados?.primeira_secao.background.sizes['1920x1080']}
            >
              <img
                className="logo"
                src={dados?.primeira_secao.logo.sizes['500x100']}
                alt={dados?.primeira_secao.logo.title}
              />
              <div
                className="titulo"
                dangerouslySetInnerHTML={{
                  __html: dados?.primeira_secao.titulo,
                }}
              />
              <div
                className="paragrafo"
                dangerouslySetInnerHTML={{
                  __html: dados?.primeira_secao.paragrafo,
                }}
              />
              <img
                className="apresentacao"
                src={dados?.primeira_secao.imagem_computador.sizes['1000x700']}
                alt={dados?.primeira_secao.imagem_computador.title}
              />
            </PrimeiraSecao>
          )}

          {dados.segunda_secao.ativar && (
            <ScrollableAnchor id="download">
              <SegundaSecao
                bg={dados?.segunda_secao.background.sizes['1920x1080']}
              >
                <div
                  className="titulo"
                  dangerouslySetInnerHTML={{
                    __html: dados?.segunda_secao.titulo,
                  }}
                />
                <div
                  className="paragrafo"
                  dangerouslySetInnerHTML={{
                    __html: dados?.segunda_secao.paragrafo,
                  }}
                />
                <div className="box-buttom-download">
                  {dados.segunda_secao.botoes.map(botao => (
                    <a
                      key={botao.botao.title}
                      href={botao.botao.url + search}
                      target={botao.botao.target}
                    >
                      <div className="buttom-download">
                        <img
                          src={botao.icone.sizes.thumbnail}
                          alt={botao.icone.title}
                        />
                        <p>{botao.botao.title}</p>
                      </div>
                    </a>
                  ))}
                </div>
              </SegundaSecao>
            </ScrollableAnchor>
          )}

          {dados.terceira_secao.ativar && (
            <TerceiraSecao
              bg={dados?.terceira_secao.background.sizes['1920x1080']}
            >
              <div className="box-conteudo">
                {dados.terceira_secao.conteudos.map(conteudo => (
                  <div>
                    <div
                      className="titulo"
                      dangerouslySetInnerHTML={{
                        __html: conteudo.titulo,
                      }}
                    />
                    <div
                      className="paragrafo"
                      dangerouslySetInnerHTML={{
                        __html: conteudo.paragrafo,
                      }}
                    />
                  </div>
                ))}
              </div>
              <div className="box-video">
                <div
                  className="video-responsive"
                  dangerouslySetInnerHTML={{
                    __html: dados?.terceira_secao?.video,
                  }}
                />
              </div>
            </TerceiraSecao>
          )}

          {dados.quarta_secao.ativar && (
            <>
              {dados.quarta_secao.itens.map(item => (
                <QuartaSecao key={item.titulo}>
                  <div>
                    <img
                      className="item-img"
                      src={item.imagem.sizes['1000x700']}
                      alt={item.imagem.title}
                    />
                  </div>
                  <div className="box-conteudo">
                    <img
                      className="icone"
                      src={item.icone.sizes.thumbnail}
                      alt={item.icone.title}
                    />
                    <div
                      className="titulo"
                      dangerouslySetInnerHTML={{
                        __html: item.titulo,
                      }}
                    />
                    <div
                      className="paragrafo"
                      dangerouslySetInnerHTML={{
                        __html: item.paragrafo,
                      }}
                    />
                  </div>
                </QuartaSecao>
              ))}
            </>
          )}

          {dados.quinta_secao.ativar && (
            <QuintaSecao>
              {dados.quinta_secao.botoes.map(botao => (
                <a
                  className="btn-primario"
                  href={botao.botao.url + search}
                  target={botao.botao.target}
                >
                  {botao.botao.title}
                </a>
              ))}
            </QuintaSecao>
          )}

          {dados.sexta_secao.ativar && (
            <SextaSecao>
              <div className="stars">
                <img src={Stars} alt="" />
              </div>
              <div className="nota">
                <div
                  className="titulo"
                  dangerouslySetInnerHTML={{
                    __html: dados.sexta_secao.nota_do_app,
                  }}
                />
                <p>{dados.sexta_secao.paragrafo}</p>
              </div>

              <Carousel {...getConfigurableProps()}>
                {dados.sexta_secao.depoimentos.map(depoimento => (
                  <div className="item">
                    <img
                      src={depoimento.avatar.sizes.thumbnail}
                      alt={depoimento.avatar.title}
                    />
                    <div
                      className="paragrafo"
                      dangerouslySetInnerHTML={{
                        __html: depoimento.paragrafo,
                      }}
                    />
                    <div
                      className="nome"
                      dangerouslySetInnerHTML={{
                        __html: depoimento.nome,
                      }}
                    />
                    <div
                      className="aprovacao_ou_ano_atual"
                      dangerouslySetInnerHTML={{
                        __html: depoimento.aprovacao_ou_ano_atual,
                      }}
                    />
                  </div>
                ))}
              </Carousel>
            </SextaSecao>
          )}

          {dados.setima_secao.ativar && (
            <SetimaSecao>
              <img
                src={dados.setima_secao.imagem.sizes['1000x700']}
                alt={dados.setima_secao.imagem.title}
              />
              <div
                className="paragrafo"
                dangerouslySetInnerHTML={{
                  __html: dados.setima_secao.paragrafo,
                }}
              />
            </SetimaSecao>
          )}

          {dados.oitava_secao.ativar && (
            <QuintaSecao>
              <a
                className="btn-primario"
                href={dados.oitava_secao.botao.url + search}
                target={dados.oitava_secao.botao.target}
              >
                {dados.oitava_secao.botao.title}
              </a>
            </QuintaSecao>
          )}
        </>
      )}
    </>
  );
};

export default Aplicativo;
